@mixin item-block {
    position: relative;
    width: 100%;
    color: white;
    margin: 0 0 20px;
    font-family: 'ralewaylight', Arial, sans-serif;
}

.fade-transition {
    @include transition(opacity, 0.4s);
}

@mixin title {
    font-family: "brandon_grotesquemedium", Arial, sans-serif;
    color: white;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: .2em;
}

#sticky-toolbar {
    
    transform: translateZ(0);
    z-index: 100;

    &.is-sticky {
        margin-left: 0;
    }
}
#aq-composer-wrapper {
    .grid-container {
        margin: 0;
        padding: 0;
        max-width: none;
    }
    .composer-content,
    .composer-toolbar {
        padding: 20px 0;
        .composer-title {
            @include title;
            margin-bottom: 10px;
            text-align: center;
        }
        .composer-help {
            color: #9f9f9f;
            font-family: 'ralewaylight', Arial, sans-serif;
            margin-bottom: 10px;
        }
        .block-item {
            @include item-block;

            -webkit-transition: all 1s ease;
    max-height: auto;

            &.block-template {
                font-size: 14px;
                letter-spacing: .1em;
                border: 1px dashed white;
                text-align: center;
                background-color: $purple-dark;
                color: white;
                padding: 15px;
                cursor: move;
                @include zoomOnHover;
            }
            &.block-item-collapsed {
                max-height: 35px;
                overflow: hidden;
            }
            &:last-child {
                margin-bottom: 0;
            }
            &.image-text,
            &.double-image {
                .right-column {
                        padding-right: 0;
                    }
                .left-column {
                        padding-left: 0;
                    }
                }
            &.double-image {
                .columns-container {
                    margin-bottom: 10px;
                    &:after {
                        clear: both;
                    }
                }
            }
            .block-task {
                opacity: 0;
                transition: opacity 0.7s;
                position: absolute;
                padding: 5px 5px 2px;
                top: 0;
                right: 0;
                z-index: 99999;
                .block-task-icon {
                    @include icomoon;
                    color: white;
                    &.task-move {
                        cursor: move;
                        &:before {
                            content: "\f047";
                        }
                    }
                    &.task-delete {
                        cursor: pointer;
                        &:before {
                            content: "\f014";
                        }
                    }
                }
            }
            &:hover {
                .block-task {
                    opacity: 1;
                }
            }
        }
    }
    .composer-content {
        background-color: $purple-darken;
        margin-top: 0;
        .block-item {
            padding-top: 10px;
            hr {
                border: none;
                border-top: 1px dotted white;
                margin: 40px 0px;
            }
        }
    }
    .composer-toolbar {
        text-align: center;
        padding: 20px 25px;
    }
}

button.btn {
    padding: 20px;
    cursor: pointer;
    margin: 20px 0;
    background: $purple-dark;
    border: none;
    color: white;
    -webkit-transition-duration: 0.4s;
    /* Safari */
    transition-duration: 0.4s;
    @include zoomOnHover;
}

.field-wrapper {
    margin-bottom: 15px;
    clear: both;
    position: relative;
    &:last-child {
        margin-bottom: 0;
    }
    .field-label-container {
        margin-bottom: 5px;
        @include title;
    }
    .composer-field {
        font-family: 'ralewayregular', Arial, sans-serif;
        font-size: 14px;
        color: #9f9f9f;
        letter-spacing: .05em;
        padding: 15px;
        line-height: initial;
        box-shadow: 0 0 0 0 transparent;
        border: none;
        border-radius: 1%;
        background: $purple-dark;
        line-height: 20px;
        width: 100%;
        &.select {
            cursor: pointer;
        }
        &.tinymce {
            min-height: 80px;
        }
        &.textbox,
        &.select,
        &.tinymce {
            @include zoomOnHover;
        }
        &:focus {
            outline: none;
        }
    }
}

.map-fake {
    height: 1px;
    width: 1px;
    opacity: 0;
}

.algolia-autocomplete {
    width: 100%;
    .aa-input,
    .aa-hint {
        width: 100%;
    }
    .aa-hint {
        color: #999;
    }
    .aa-dropdown-menu {
        width: 100%;
        background-color: #fff;
        border: 1px solid #999;
        border-top: none;
        z-index: 9999 !important;
        .aa-suggestion {
            cursor: pointer;
            padding: 5px 4px;
            color: $purple-darken;
            em {
                font-weight: bold;
                font-style: normal;
            }
        }
        .aa-suggestion.aa-cursor {
            background-color: #B2D7FF;
        }
    }
}