.image-upload-container {
    width: 100%;
    position: relative;
    .upload-btn {
        border: 1px dashed white;
        background: $purple-dark;
        background-repeat: no-repeat;
        background-position: center;
        width: 100%;
        font-family: 'ralewayregular', Arial, sans-serif;
        font-size: 14px;
        color: #9f9f9f;
        letter-spacing: .05em;
        &:hover {
            color: white;
        }
    }
    .progress-bar-container {
        margin-top: 5px;
        width: 90%;
        height: 15px;
        border: 1px solid $purple-darken;
        opacity: 0;
        @include transition(opacity, $default-timing, ease);
        .progress-bar {
            width: 0px;
            background-color: $purple-light;
            color: white;
            height: 100%;
            font-size: 10px;
            line-height: 10px;
            text-align: center;
            padding: 1px;
        }
    }
    .msgbox {
        padding: 5px 0px;
    }
    .tasks {
        position: absolute;
        bottom: 20px;
        right: 15px;
        width: 200px;
        height: 20px;
        text-align: right;
        .task {
            cursor: pointer;
            &.task-delete {
                display: none;
            }
            &.task-preview {
                display: none;
            }
        }
    }
}